.todo-list-item {
  font-size: 1.25rem;
  display: flex;
  justify-content: space-between;
  button {
    width: 35px;
    margin: 3px; }
  &.done {
    .todo-list-item-label {
      text-decoration: line-through; } }
  &.important {
    .todo-list-item-label {
      font-weight: bold;
      color: steelblue; } }
  &.hide {
    display: none; } }

.todo-list-item-label {
  margin-left: 1.25rem;
  line-height: 35px;
  cursor: pointer;
  user-select: none; }
